section.navbar {
    box-shadow: 0px 21px 27.700000762939453px 0px rgba(0, 0, 0, 0.59);
    background: #1F2021;
}

section.navbar .overlay{
    position: absolute;
    top: 100%;
    right: 0;
    width: 100vw;
    height: 100vh;
    background: #00000073;
    z-index: 9999;
    overflow: hidden;
    transition: all 0.3s;
}

nav {
    display: flex;
    align-items: center;
    background-color: transparent;
    padding: 1rem 2rem;
    width: 100%;
}

nav .bars {
    height: 21px;
    padding: 2px 0;
    cursor: pointer;
    align-items: center;
    justify-content: center;
}

nav .bars .bar {
    width: 27px;
    height: 3px;
    background-color: #fff;
    position: relative;
    transition: all 0.3s;
}

nav .bars .bar::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #fff;
    position: absolute;
    top: -7px;
    left: 0;
    transition: all 0.3s;
}

nav .bars .bar::after {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #fff;
    position: absolute;
    bottom: -7px;
    left: 0;
    transition: all 0.3s;
}

nav .bars.open .bar {
    background-color: transparent;
}

nav .bars.open .bar::before {
    top: 0;
    transform: rotate(45deg);
}

nav .bars.open .bar::after {
    bottom: 0;
    transform: rotate(-45deg);
}

nav .logo {
    width: 100px;
}

nav ul {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 2rem;
}


nav ul li a {
    display: block;
    text-decoration: none;
    color: #fff;
    font-size: 16px;
    padding: 0.3rem 1rem 0.4rem;
    border-radius: 4px;
    transition: all 0.3s;

}

nav ul li a:hover, nav .navLinks li a.active:hover {
    background-color: var(--Blue300);
}

nav .navLinks li a.active {
    background-color: #53acff9c;
}

nav .AuthButtons a.signUp {
    outline: none;
    color: #fff;
    background: transparent;
    border: none;
    padding: 0.3rem 1.2rem 0.4rem;
    background-color: var(--Blue300);
    border: 1px solid var(--Blue300);
    border-radius: 4px;
    font-size: 12px;
}

nav .AuthButtons a.logIn {
    outline: none;
    color: #fff;
    background: transparent;
    border: none;
    padding: 0.3rem 1.2rem 0.4rem;
    border: 1px solid #fff;
    border-radius: 4px;
    font-size: 12px;

}

nav .navLinks.first{
    background-color: #53acff98;
    border-radius: 5px;
    overflow: hidden;
}

nav .navLinks.first a{
    border-radius: 0;
}

@media only screen and (max-width: 1024px) {

    nav {
        padding: 0.75rem;
    }
    nav .logo img{
        width: 85px;
    }

    nav .mobileScreenNav {
            display: flex ;
            flex-direction: column ;
            align-items: flex-start;
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            background: #1F2021;
            padding: 1rem;
            box-shadow: inset 0px 8px 17px -5px #00000087;
            z-index: -1;
            transform: translateY(-100%);
            visibility: hidden;
            /* transition: all 0.3s; */
    }

    nav .mobileScreenNav.open{
        z-index: 99999;
        transform: translateY(0);
        visibility: visible;
    }

    nav .mobileScreenNav .navLinks{
        flex-direction: column;
        align-items: flex-start;
    }

    nav .mobileScreenNav .navLinks.first {
        background-color: transparent;
        align-items: flex-start;
    }

    nav .mobileScreenNav .navLinks.first a {
        background-color: #53acff98;
        border-radius: 5px;
    }

    nav .mobileScreenNav .navLinks.first a:hover {
        background-color: var(--Blue300);
    }

}




@media only screen and (min-width: 2224px) {

    nav ul li a {
        font-size: 28px;
    }
    nav .AuthButtons button.logIn , nav .AuthButtons button.signUp{
        font-size: 22px;
        padding: 0.7rem 2rem;
    }
    nav .logo {
        width: 144px;
    }

    nav .logo img{
        width: 100%;
    }

}